import React, { useState, useEffect, useContext } from "react";
import { NavLink, useLocation } from "react-router-dom";
import cx from "classnames";
import nb from "../sass/components/navbar.module.scss";
import globalStateContext from "../context/globalStateContext";

const routes = [
    { uri: "/", name: "Home" },
    { uri: "/about", name: "About Us" },
    // { uri: "/contact", name: "Contact" },
];

const nav_menu = routes.map(({ uri, name }, i) => (
    <li className="nav-item" key={i}>
        <NavLink className="nav-link" exact to={uri}>
            {name}
        </NavLink>
    </li>
));

const NavBar = () => {
    const [expand, SetExpand] = useState(false);
    let location = useLocation();
    let { lang } = useContext(globalStateContext);

    const toggleMobileNavBar = (status) => {
        if (status === true) {
            document.querySelector(`.${nb.nav_content_cover}`).style.display =
                "block";
            setTimeout(() => {
                SetExpand(true);
            }, 100);
        } else {
            setTimeout(() => {
                document.querySelector(
                    `.${nb.nav_content_cover}`
                ).style.display = "none";
                SetExpand(false);
            }, 100);
        }
    };

    useEffect(() => {
        toggleMobileNavBar(false);
    }, [location]);

    return (
        <nav
            className={cx(
                "navbar navbar-expand-lg navbar-light bg-white fixed-top shadow-sm",
                nb.navbar
            )}
        >
            <div className="container">
                <NavLink to="/" className="navbar-brand">
                    <img
                        src="/images/logo.png"
                        alt="ProLab MM"
                        className={nb.navbar_logo}
                    />{" "}
                    ProLab MM
                </NavLink>
                <button
                    className="navbar-toggler"
                    type="button"
                    aria-label="Open Menu"
                    onClick={() => {
                        toggleMobileNavBar(true);
                    }}
                >
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div
                    className={cx(
                        "collapse navbar-collapse",
                        nb.main_menu,
                        expand ? nb.expanded : null
                    )}
                    id="main-navigation"
                >
                    <ul className="navbar-nav ml-auto">
                        <span
                            className={nb.close_nav}
                            aria-label="Close Menu"
                            onClick={() => {
                                toggleMobileNavBar(false);
                            }}
                        ></span>
                        {nav_menu}
                        <li className="nav-item d-none d-lg-block">
                            <span className="nav-link">|</span>
                        </li>
                        <li className="nav-item">
                            <button
                                onClick={() => {
                                    lang.set("en");
                                    toggleMobileNavBar(false);
                                }}
                                className="btn btn-link nav-link btn-block text-left rounded-0"
                            >
                                En
                            </button>
                        </li>
                        <li className="nav-item">
                            <button
                                onClick={() => {
                                    lang.set("mm");
                                    toggleMobileNavBar(false);
                                }}
                                className="btn btn-link nav-link btn-block text-left rounded-0"
                            >
                                Mm
                            </button>
                        </li>
                    </ul>
                </div>
                <div
                    className={cx(
                        nb.nav_content_cover,
                        expand ? "d-block" : "d-none"
                    )}
                    onClick={() => {
                        toggleMobileNavBar(false);
                    }}
                ></div>
            </div>
        </nav>
    );
};

export default NavBar;
